import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter);

import App from './App';
const Home = resolve => require(['./components/home.vue'], resolve);
const News = resolve => require(['./components/news/'], resolve);
const Portfolio = resolve => require(['./components/portfolio/'], resolve);
const Loading = resolve => require(['./components/loading'], resolve);
const Member = resolve => require(['./components/member/'], resolve);
const MemberResume = resolve => require(['./components/member/resume'], resolve);
const MemberPhoto = resolve => require(['./components/member/photo'], resolve);
const MemberProduct = resolve => require(['./components/member/product'], resolve);
const MemberVideo = resolve => require(['./components/member/video'], resolve);
const Aboute = resolve => require(['./components/aboute'], resolve);

// import 'bootstrap/dist/css/bootstrap.css'
import './assets/css/reset.less'
import './assets/css/public.less'
import './assets/css/font-awesome.min.css'
import './assets/css/response.less'
import store from './store/index'

const routes = [
  {path : '/', component : Home, params:{active:true} },
  {path : '/news', component : News },
  {path : '/portfolio',           component : Portfolio },
  {path : '/portfolio/:year/review', component : Portfolio },
  {path : '/member', component : Member },
  {path : '/member/:user', component : MemberResume },
  {path : '/member/:user/product', component : MemberProduct },
  {path : '/member/:user/photo', component : MemberPhoto },
  {path : '/member/:user/video', component : MemberVideo },
  {path : '/loading', component : Loading },
  {path : '/aboute', component : Aboute },
  {path : '*', component : Home }
];

const router = new VueRouter({
  // mode: 'history',
  saveScrollPosition: true,
  routes
});

// router.beforeEach(({meta, path}, from, next) => {
//   var { auth = true } = meta;
//   var isLogin = Boolean(store.state.login.token) //true用户已登录， false用户未登录

//   if (auth && !isLogin && path !== '/login') {
//     return next({ path: '/login' })
//   }
//   if(isLogin && (path == '/login' || path == '/reg')){
//    return next({ path: '/person' })
//    }
//   next()
// });

var app = new Vue({
  el: '#app',
  router,
  store,
  ...App,
});



